    background-image: image-set(
            url('../img/call.avif') 1x,
            url('../img/call.webp') 1x,
            url('../img/call.png') 1x);
    background-size: contain;
    background-repeat: no-repeat;
    width: 24px;
    height: 24px;
}

.email::before {
