      url('../img/project5.jfif') 1x);
  width: 380px;
  height: 450px;
  background-size: cover;
  background-repeat: no-repeat;
  object-position: center center;
  overflow: hidden;
}

.our-project__title-card_white {
  color: var(--bg-white-color, #F2F0F1);
}

.our-project__title-card_position {
  position: absolute;
  max-width: 360px;
  bottom: 40px;
  left: 10px;
}

.our-project__link-card_position {
  position: absolute;
  bottom: 40px;
  right: 10px;
}

.our-project__title-card_mini {
  font-size: 20px;
  line-height: 26px;
  margin-bottom: 22px;
}

.our-project__article-mini>.our-project__link-card {
  display: none;
  text-align: right;
}

.our-project__link {
  border: 1px solid transparent;
}

.our-project__link:active,
.our-project__link:focus {
  border: 1px solid var(--text-color-orange, #ff8a00);
}


@media (max-width: 1200px) {
  .our-project__article {
    grid-template-columns: minmax(160px, 200px) auto;
    gap: 7px 20px
  }

  .our-project__grid {
    column-gap: 20px;
    row-gap: 33px;
  }

  .our-project__img-card {
    width: 200px;
    height: 130px;
  }

  .our-project__title-card {
    font-size: 24px;
    line-height: 30px;
  }

  .our-project__text-card {
    font-size: 18px;
    line-height: 26px;
  }

  .our-project__article-big {
    width: 280px;
    height: 332px;
  }

  .our-project__title-card_position {
    bottom: 46px;
  }

  .our-project__link-card_position {
    bottom: 0;
  }

  .our-project__title-card_mini {
    font-size: 18px;
    line-height: 24px;
  }

  .our-project__other-project {
    grid-template-columns: repeat(3, 80px);
    grid-template-rows: repeat(2, 70px);
    grid-auto-rows: 0;
  }

  .our-project__article-mini>.our-project__link-card {
    display: block;
  }
}

@media (max-width: 940px) {
  .our-project__article {
    grid-template-columns: minmax(130px, 160px) auto;
    gap: 3px 10px
  }

  .our-project__grid {
    column-gap: 80px;
    row-gap: 20px;
  }

  .our-project__img-card {
    width: 160px;
    height: 104px;
  }

  .our-project__title-card {
    font-size: 18px;
    line-height: 24px;
  }

  .our-project__text-card-big {
    display: none;
  }

  .our-project__text-card-middle {
    display: block;
  }

  .our-project__article-big {
    width: 220px;
    height: 271px;
  }

  .our-project__title-card_position {
    bottom: 39px;
  }

  .our-project__link-card_position {
    bottom: 25px;
  }

  .our-project__title-card_mini {
    font-size: 14px;
    line-height: 20px;
  }

  .our-project__other-project {
    grid-template-columns: repeat(3, 60px);
    grid-template-rows: repeat(2, 52px);
    gap: 18px 20px;
  }
}

@media (max-width: 650px) {

  .our-project__grid {
    display: grid;
    grid-template-columns: auto;
    grid-template-rows: repeat(3, auto);
    row-gap: 30px;
    grid-template-areas: none;
  }

  .our-project__article {
    gap: 10px;
  }

  .our-project__img-card {
    width: 130px;
    height: 134px;
  }

  .our-project__title-card {
    font-size: 16px;
    line-height: 22px;
  }

  .our-project__text-card-middle {
    display: none;
  }

  .our-project__text-card-mini {
    display: block;
  }

  .our-project__article-big {
    width: 270px;
    height: 320px;
    margin: 0 auto;
  }

  .our-project__title-card_position {
    bottom: 51px;
  }

  .our-project__link-card_position {
    bottom: 19.5px;
  }

  .our-project__title-card_mini {
    font-size: 14px;
    line-height: 20px;
    margin-bottom: 10px;
  }

  .our-project__other-project {
    grid-template-columns: repeat(4, 60px);
    grid-template-rows: repeat(1, 52px);
    column-gap: 10px;
    grid-auto-rows: 0;
  }

  .our-project__article-mini {
    max-width: 270px;
    margin: 0 auto;
  }
}
.burger-menu {
  position: fixed;
  top: 90px;
  bottom: 0;
  right: 0;
  left: 130px;
  overflow: hidden;
  overflow-y: auto;
  z-index: 99;
  padding-top: 30px;
}

.burger-menu__burger {
  padding: 70px 40px 50px 20px;
  background-color: var(--bg-grey-color, #f2f0f1);
  border-bottom: 10px solid var(--text-color-orange, #ff8a00);
}

.burger-menu__wrapper {
  max-width: 394px;
}

.burger-menu__link-text {
  display: block;
  font-size: 20px;
  line-height: 30px;
  font-weight: 550;
  text-align: left;
  padding: 15px 0 15px 20px;
}

.burger-menu__item:not(:last-child) {
  border-bottom: 1px solid var(--text-color-orange, #ff8a00);
}

.burger-menu__button {
  display: none;
}

.burger-menu__burger-list {
  display: flex;
  flex-direction: column;
  align-items: center;
  gap: 0px;
}

.burger-menu__item {
  width: 100%;
}

.burger-menu__rect {
  position: absolute;
  top: 3px;
  z-index: -1;
}

@media (max-width: 1200px) {
  .burger-menu {
    left: 72px;    
  }

}

@media (max-width: 940px) {
  .burger-menu {
    left: 34px;
  }
}

@media (max-width: 650px) {
  .burger-menu {
    left: 25px;
    top: 72px;
  }

  .burger-menu__wrapper {
    max-width: 270px;
  }
}